html,
body {
  margin: 0;
  padding: 0;
  height: 100%;
  color: #333;
  font-size: 16px;
}

h1,
h2,
h3,
h4,
h5,
h6,
.header {
  color: #b144ab;
  text-shadow: 0 0 1px #fff;
}
h1 svg,
h2 svg,
h3 svg,
h4 svg,
h5 svg,
h6 svg,
.header svg {
  fill: #b144ab;
  width: 18px;
  height: 18px;
  margin-right: 10px;
}

h1,
h2,
h3,
h4,
h5,
h6,
ul,
ol,
p {
  margin: 16px 0;
  padding: 0;
  list-style-type: none;
}

h1 {
  margin: 25px 0;
}
h1 svg {
  width: 24px;
  height: 24px;
}

abbr {
  cursor: help;
}

a {
  color: #0da1b8;
  text-decoration: none;
  transition: background-color, border-color, color, fill;
  transition-duration: 0.2s;
  transition-timing-function: ease-out;
}
a:hover {
  text-decoration: underline;
}

iframe {
  display: block;
  max-width: 100%;
  margin: 16px 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
  border: none;
}

h1 {
  font-size: 36px;
}

h2 {
  font-size: 28px;
}

h3 {
  font-size: 20px;
}